.page {
    background-color: rgb(237, 237, 237);

    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100vh;
}

.page section {
    padding: 25px;
    margin: 0 auto;
    max-width: 900px;
    font-size: 14pt;
    line-height: 20pt;
}

.page .combo {
    overflow: hidden;

    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: min-content;
    gap: 10px 10px;
}

.page .combo img {
    height: 100%;
    overflow: hidden;
}

.hirepage {
    .button {
        margin: 0 auto;
        font-size: 15pt;

        #right-arrow {
            fill: black;
            margin-left: 5px;
        }
    }

    .button:hover {
        background-color: #449edabd;
        cursor: pointer;
    }
}